.p-nav__list {
    display: flex;
    flex-flow: row nowrap;
    max-width: 580px; }


.p-nav__item {
    flex: 0 1 calc(100% / 4);
    margin-left: var(--spacing-s);
    text-align: center;
    font-size: var(--font-size-normal);
    transition: all 0.4s ease;
    height: 100%;
    &:nth-child(1) {
        margin-left: 0; } }


.p-nav__target {
    position: relative;
    display: inline-block;
    padding: var(--spacing-x-s);
    color: var(--font-color-whites);
    &::after {
        position: absolute;
        display: block;
        width: 0;
        height: 1px;
        left : 0%;
        bottom: 0;
        content: "";
        background-color: var(--font-color-white);
        transition: all 0.4s ease;
        transform: translateX(-0%); }
    @include lg {
        &:hover::after {
            width: 100%; } } }
