body {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    line-height: 1.6;
    font-family: 'arial',"Hiragino Kaku Gothic ProN","メイリオ",'sans-serif';
    font-feature-settings: "palt";
    font-size: 16px;
    font-weight: 400;
    color: #fff; }


* {
    box-sizing: border-box; }


img {
    max-width: 100%;
    height: auto;
    vertical-align: middle; }


$pc: 1025px;


$tab: 1024px;


$sp: 414px;


@mixin lg {
    @media (min-width: ($pc)) {
        @content; } }


@mixin md {
    @media (max-width: ($tab)) {
        @content; } }


@mixin sm {
    @media (max-width: ($sp)) {
        @content; } }


 :root {
    --font-size-sm: 14px;
    --font-size-normal: 16px;
    --font-size-lg: 24px;
    --font-size-x-lg: 32px;
    --font-size-xx-lg: 48px;
    --font-size-xxx-lg: 64px;

    --spacing-xx-s: 4px;
    --spacing-x-s: 8px;
    --spacing-s: 16px;
    --spacing-m: 32px;
    --spacing-l: 64px;
    --spacing-x-l: 128px;
    --spacing-xx-l: 256px;

    --color-base: #161616;
    --color-main: #1f1f1f;
    --color-white: #fff;

    --font-color-white: #fff;
    --font-color-blue: #09f;
    --font-color-gray: #808080;

    --font-weight-lg: 600;
    --font-weight-x-lg: 800;

    @include md {
            --font-size-sm: 12px;
            --font-size-normal: 14px;
            --font-size-lg: 22px;
            --font-size-x-lg: 28px;
            --font-size-xx-lg: 44px;
            --font-size-xxx-lg: 56px;

            --spacing-xx-s: 3px;
            --spacing-x-s: 6px;
            --spacing-s: 12px;
            --spacing-m: 24px;
            --spacing-l: 48px;
            --spacing-x-l: 96px;
            --spacing-xx-l: 192px; }

    @include sm {
            --font-size-sm: 10px;
            --font-size-normal: 12px;
            --font-size-lg: 16px;
            --font-size-x-lg: 24px;
            --font-size-xx-lg: 32px;
            --font-size-xxx-lg: 48px;

            --spacing-xx-s: 2px;
            --spacing-x-s: 4px;
            --spacing-s: 8px;
            --spacing-m: 16px;
            --spacing-l: 32px;
            --spacing-x-l: 64px;
            --spacing-xx-l: 128px; } }


p {
    font-size: var(--font-size-normal);
    color: inherit;
    line-height: 1.6; }


h4 {
    font-size: var(--font-size-lg);
    color: inherit;
    line-height: 1.6; }


h3 {
    font-size: var(--font-size-x-lg);
    color: inherit;
    line-height: 1.6; }


h2 {
    font-size: var(--font-size-xx-lg);
    color: inherit;
    line-height: 1.6; }


h1 {
    font-size: var(--font-size-xxx-lg);
    color: inherit;
    line-height: 1.6; }

